	module.exports = {"pathContext":{"group":[{"node":{"id":"/Volumes/WEEDPORNDAILY/Kushy/Development/Kushy Blog/kushy-blog-gatsby/src/content/blog/2018/introducing-kushy-metrc-php-sdk/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Kushy METRC PHP SDK","date":"19 July, 2018","cover_image":{"publicURL":"/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611.jpg","childImageSharp":{"sizes":{"srcSet":"/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611-7cc04.jpg 310w,\n/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611-69042.jpg 620w,\n/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611-4e8db.jpg 1240w,\n/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611-50ab1.jpg 1860w,\n/static/KBlog-Metrc-PHP-SDK-b31137b3a03d0046e257a21741934611-de007.jpg 2000w"}}},"section":"blog","author":"Ryo","tags":["tools","announcements","developers"]},"fields":{"slug":"/blog/2018/introducing-kushy-metrc-php-sdk/"},"excerpt":"Keeping track of legal cannabis is one hell of a job. That's why many states that legalize medicinal and recreational cannabis license a 3rd party seed to sale tracking solution to handle the sticky task. These solutions provide businesses a place to input their activity (plant growth, transfers, sales) so the state can monitor and ensure compliance with all regulations and restrictions.  METRC  is one of these seed…"}},{"node":{"id":"/Volumes/WEEDPORNDAILY/Kushy/Development/Kushy Blog/kushy-blog-gatsby/src/content/blog/2018/the-future-of-the-kushy-api/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Kushy METRC PHP SDK","date":"19 July, 2018","cover_image":null,"section":"blog","author":"Ryo","tags":["api","announcements","developers"]},"fields":{"slug":"/blog/2018/the-future-of-the-kushy-api/"},"excerpt":"Kushy was founded with a mission to provide a scalable API for the cannabis industry. So far, our API has been only read-only, with only a few endpoints for our major content areas (shops, products, etc). We haven't actually needed to use the API in any applications, beyond minor showcases of it's GET potential. Now that's all going to change. Over the past few months we've gotten a few feature requests from…"}},{"node":{"id":"/Volumes/WEEDPORNDAILY/Kushy/Development/Kushy Blog/kushy-blog-gatsby/src/content/blog/2017/integrating-crowd-sourced-cannabis-from-tumblr/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Integrating Crowd Sourced Cannabis from Tumblr","date":"10 December, 2017","cover_image":null,"section":"blog","author":"Ryo","tags":["spotlight","developers"]},"fields":{"slug":"/blog/2017/integrating-crowd-sourced-cannabis-from-tumblr/"},"excerpt":"How do you get pictures of over 9,000 cannabis strains? You could start a very long and expensive journey of buying and documenting thousands of products. Or you can get the community pitch in their pot (pics). When it came time to fill up the Kushy database with a myriad of dank images, we looked to WeedPornDaily, an online cannabis publication and community. Users of WPD can  submit photos to the website  through a…"}},{"node":{"id":"/Volumes/WEEDPORNDAILY/Kushy/Development/Kushy Blog/kushy-blog-gatsby/src/content/blog/2017/the-start-of-the-future/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"The Start of the Future","date":"08 December, 2017","cover_image":null,"section":"blog","author":"Ryo","tags":["announcements"]},"fields":{"slug":"/blog/2017/the-start-of-the-future/"},"excerpt":"We're big fans of fast, responsive apps. There's nothing more discouraging than slugging around a website, waiting for each page to chug into place. Things should be reactive to our actions, and feel fairly seamless. The opposite of clunky. Here at Kushy, we've done our best to develop a website full of all the features we need. The one thing we've struggled with, is the  user experience . As good as our design is…"}},{"node":{"id":"/Volumes/WEEDPORNDAILY/Kushy/Development/Kushy Blog/kushy-blog-gatsby/src/content/blog/2017/kushy-blog-is-alive/index.md absPath of file >>> MarkdownRemark","frontmatter":{"title":"Kushy Blog is Alive!","date":"06 December, 2017","cover_image":null,"section":"blog","author":"Ryo","tags":["blog","kirby","cms","announcements"]},"fields":{"slug":"/blog/2017/kushy-blog-is-alive/"},"excerpt":"After much deliberation over picking a cloud-based service like Medium or Tumblr, or even a Git-powered JAM-stack with  Jekyll  or  GatsbyJS , we finally settled on a blog framework for Kushy:  KirbyCMS .  Don't get me wrong. We'll still post on  Medium  and  Tumblr . But we'll do the thing that brands like  Google Design  does, and cross-post our content for maximum visibility. Kirby the Pink Guy? Not  that  Kirby…"}}],"pathPrefix":"blog","first":true,"last":true,"index":1,"pageCount":1}}

